import SolverService from './SolverService'
import EpsilonSetSolver from './EpsilonSetSolver'

import map from '../utils/map'
import StringUtils from "../utils/StringUtils";

const EPSILON_TEST_CASES = [
    [
        'S-aBA',
        'S-BSXA',
        'B-XCAX',
        'B-BYa',
        'B-b',
        'X-abX',
        'X-',
        'A-ASbX',
        'A-AX',
        'A-',
        'C-XAAX',
        'Y-BAXB',
        'Y-abXA'
    ], [
        'S-aYBa',
        'S-BXA',
        'A-ASbX',
        'A-',
        'B-XAAX',
        'B-abC',
        'C-ABX',
        'C-abXA',
        'X-aaX',
        'X-',
        'Y-aYBAXB',
        'Y-abXA'
    ]
];

class RecursNonTermsSolver extends SolverService {

    static LEFT_RECURSION = 0;
    static RIGHT_RECURSION = 1;
    #descriptionRecursionWord = ['ліворекурсивний', 'праворекурсивний'];

    recursionType;

    constructor(theme, description) {
        super('sp', theme, description);
    }

    getTestCase(index) {
        let rules = [];
        for (let rule of EPSILON_TEST_CASES[index]) {
            let parts = rule.split('-');
            rules.push({
                left: parts[0],
                right: parts[1],
            })
        }

        return rules;
    }

    lRekyrsDfs(gr, isEpsilo, used, stack, sol, startV, v) {
        if (v === startV && used[startV] === 1) {
            for (let it of stack) sol.push(it);

            return;
        }

        for (let trans of gr[v]){
            if (sol.length !== 0) {
                return;
            }

            let oldStackSize = stack.length;

            stack.push(trans + stack[stack.length - 1].substring(1, stack[stack.length - 1].length));

            for (let i = 0; i < trans.length; i++) {
                let it = trans.charAt(i);
                if (sol.length !== 0) break;

                if ('a' <= it && it <= 'z') break;

                let nextV = it;

                if (used[nextV] === undefined) {
                    used[nextV] = 1;

                    this.lRekyrsDfs(gr, isEpsilo, used, stack, sol, startV, nextV);
                }

                if (isEpsilo[nextV] === 1) {
                    stack.push(stack[stack.length - 1].substring(1, stack[stack.length - 1].length));
                }
                else {
                    break;
                }
            }

            while (stack.length > oldStackSize) {
                stack.splice(stack.length - 1, 1);
            }
        }
    }

    solveLeftRecurs(input, stepOutputHandle) {                       //vector <pair<string, vector <string> > >, vector <string>
        let res = [];                           //vector <pair<string, vector <string> > >

        let gr = new map();                         //map <string, vector <string> >
        let isEpsilo = new map();                   //map <string, int>

        for (let it of input) {
            let pair = it.split('-');
            if(gr[pair[0]] === undefined) gr[pair[0]] = [];

            gr[pair[0]].push(pair[1]);
        }

        stepOutputHandle('Знайтемо множину ε-нетерміналів:');
        let epsiloNt = EpsilonSetSolver.solve(input, stepOutputHandle, () => {});          //vector <string>

        stepOutputHandle('Рекурсія у правилах переходу:');
        for (let it of epsiloNt) {
            isEpsilo[it] = 1;
        }

        for (let it in gr) {
            let used = new map();                  //map <string, int>
            let stack = [];              //vector <string>
            let sol = [];                //vector <string>

            stack.push(it);

            let startV = it;                       //string

            this.lRekyrsDfs(gr, isEpsilo, used, stack, sol, startV, it);

            res.push({  first: it,
                        second: sol });
        }

        return res;
    }

    solve(dat, stepOutputHandle, answerOutputHandle) {                 //vector <string>
        let isPravo = this.recursionType;

        if (isPravo === 1) {
            for (let it in dat) {
                dat[it] = StringUtils.reverse(dat[it], 2, dat[it].length);
            }
        }

        let res = this.solveLeftRecurs(dat, stepOutputHandle);   //vector <pair<string, vector <string> > >

        if (isPravo === 1) {
            for (let it of res) {
                if (it.second.length === 0) {
                    continue;
                }

                for (let itt in it.second) {
                    it.second[itt] = StringUtils.reverse(it.second[itt], 0, it.second[itt].length);
                }
            }
        }

        res.sort((p1, p2) => p1.first.localeCompare(p2.first));

        let finalResult = '';
        for (let it of res) {
            if (it.second.length === 0) {
                finalResult += it.first + ' - не є ' + this.#descriptionRecursionWord[this.recursionType] + ' нетерміналом<br>';
                stepOutputHandle(it.first + ' - не є ' + this.#descriptionRecursionWord[this.recursionType]);
            }
            else {
                let output = it.second[0];
                for (let i = 1; i < it.second.length; i++) {
                    output += ' ➔ ' + it.second[i];
                }

                finalResult += it.first + ' - є ' + this.#descriptionRecursionWord[this.recursionType] + ' нетерміналом<br>';
                stepOutputHandle(output);
            }
        }

        answerOutputHandle(finalResult);
    }
}

export default RecursNonTermsSolver;