import RecursNonTermsSolver from './RecursNonTermsSolver'

class RightRecursNonTermsSolver extends RecursNonTermsSolver{

    constructor() {
        super('r-recurs-non-terms', 'побудувати множину праворекурсивних нетерміналів');
        this.recursionType = RecursNonTermsSolver.RIGHT_RECURSION;
    }

}

let instance = new RightRecursNonTermsSolver();

export default instance;