import SolverService from './SolverService'
import ArrayUtils from "../utils/ArraysUtils";
import SetUtils from "../utils/SetUtils";
import MapUtils from "../utils/MapUtils";

const EPSILON_TEST_CASES = [
    [
        'S-AaB',
        'A-bEA',
        'A-a',
        'B-bB',
        'B-',
        'E-SB',
        'E-Sa'
    ], [
        'S-AbC',
        'A-DbC',
        'C-Sa',
        'C-',
        'D-aD',
        'D-'
    ]
];

class FirstKSetSolver extends SolverService {

    constructor() {
        super('sp', 'first_k', 'побудувати множину First<t class="lwc">2</t>()');
    }

    getTestCase(index) {
        let rules = [];
        for (let rule of EPSILON_TEST_CASES[index]) {
            let parts = rule.split('-');
            rules.push({
                left: parts[0],
                right: parts[1],
            })
        }

        return rules;
    }

    solve(input, stepOutputHandle, answerOutputHandle) { //map <string, vector<string>>
        let k = 2, iteration = 0;
        let trans = {};         //map <string, vector<string>>
        for (let t of input) {
            let nonTerminal = t.substring(0, 1);
            let word = t.substring(2);

            if (trans[nonTerminal] === undefined) {
                trans[nonTerminal] = [];
            }

            trans[nonTerminal].push(word);
        }

        let sol = [];           //vector<map<string, vector<string>>>
        sol.push({});           //map<string, vector<string>> ()

        for (; ;) {
            let nextIter = {};      //nextIter - result on this iteration //map <string, vector<string>>
            let nextIterSet = {};   // nextIterSet - result on this iteration, but use set to prevent repetitions, map <string, set<string>>

            for (let it in trans) {
                for (let rule of trans[it]) {
                    let inf = [];                //vector <string>
                    inf.push('');

                    for (let i = 0; i < rule.length; i++) {
                        let letter = rule.charAt(i);
                        if ('a' <= letter && letter <= 'z') {
                            for (let j = 0; j < inf.length; j++) inf[j] = inf[j] + letter;
                        } else {
                            let nextInf = [];       //vector <string>
                            for (let itt of inf) {
                                if (sol[sol.length - 1] !== undefined && sol[sol.length - 1][letter] !== undefined) {
                                    for (let suff of sol[sol.length - 1][letter]) {
                                        nextInf.push(itt + suff);
                                    }
                                }
                            }

                            inf = nextInf;
                        }
                    }

                    for (let itt of inf) {
                        if (nextIterSet[it] === undefined) nextIterSet[it] = [];
                        if (itt.length <= k) {
                            SetUtils.insert(nextIterSet[it], itt);
                        } else {
                            // if .size() > k than take only first k letters
                            SetUtils.insert(nextIterSet[it], itt.substring(0, k));
                        }
                    }
                }
            }

            for (let it in nextIterSet) {
                for (let itt of nextIterSet[it]) {
                    if (nextIter[it] === undefined) nextIter[it] = [];
                    nextIter[it].push(itt);
                }
            }

            if (MapUtils.compare(sol[sol.length - 1], nextIter)) {
                sol.push(nextIter);

                break;
            }

            sol.push(nextIter);
            iteration++;
        }

        let cnt = 1;
        let output;
        for (let it of sol) {
            if(MapUtils.compare(it, {})) continue;
            output = '';

            let sortedSolutions = []
            for(let key in it) sortedSolutions.push(key);
            sortedSolutions.sort();

            for (let key of sortedSolutions) {
                output += key + ': ' + ArrayUtils.printSet(it[key]) + '; &nbsp;';
            }

            stepOutputHandle('Ітерація ' + cnt + ':<br>' + output);
            cnt++;
        }

        answerOutputHandle('Множина first<t class="lwc">2</t>() для вказаної граматики:<br>' + output);

        return sol[sol.length - 1];
    }
}

let instance = new FirstKSetSolver();

export default instance;