import RecursNonTermsSolver from './RecursNonTermsSolver'

class LeftRecursNonTermsSolver extends RecursNonTermsSolver{

    constructor() {
        super('l-recurs-non-terms', 'побудувати множину ліворекурсивних нетерміналів');
        this.recursionType = RecursNonTermsSolver.LEFT_RECURSION;
    }

}

let instance = new LeftRecursNonTermsSolver();

export default instance;