import SetUtils from "./SetUtils";

class MapUtils {

    static compare(as, bs) {
        for(let key in as) {
            if(bs[key] === undefined || !SetUtils.compare(as[key], bs[key])) return false;
        }

        for(let key in bs) {
            if(as[key] === undefined || !SetUtils.compare(as[key], bs[key])) return false;
        }

        return true;
    }

}

export default MapUtils;